.recipes {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 60%;
    max-width: 750px;
    margin-top: 10px;
}

.ulListRecipe{
    list-style: none;
    width: 60%;
}
.listRecipes:hover {
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    transition: box-shadow 0.3s ease-in-out;
    cursor: pointer;
    background-color: #DAA520;
}
.listRecipes {
    color: black;
    box-shadow: 0.25rem 0.25rem 0.6rem rgba(0,0,0,0.5), 0 0.5rem 1.125rem rgba(75,0,0,0.5);
    margin-top: 1rem;
    min-height: 3rem;
    padding: 1rem 1rem 1rem 1rem;
    position: relative;
    font-size: 20px;
    display: flex;
    justify-content: center;
}


.listRecipesLink, listRecipesLink:visited, listRecipesLink:active {
    color: black;
    text-decoration: none;
}


.travel{
    display: flex;
}


.travelByCity {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 60%;
    max-width: 750px;
}

.info {
    display: flex;
    text-align: left;
}

img {
    padding: 10px 0px;
    max-width: 100%;
    max-height: 10%;
}

h1 {
    padding-bottom: 10px;
}
 
h2 {
    padding-bottom: 10px;
}
.recipeInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 65%;
    max-width: 750px;
    margin-top: 10px;
    background-color: #fff;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 1rem;
}
.test {
    background-color: #fff;
    display: flex;
}
.content {
    margin-bottom: 30px;
}
.steps {
    counter-reset: count;
    list-style: none;
    margin: 1.75rem 0;
    padding-left: 1rem;
    width: 100%;
}
.ingredients {
        font-size: 17px;
    letter-spacing: .5px;
    line-height: 28px;
}

.origin {
    margin: 5px 0px;
    font-style: italic;
}
.step {
    background: white;
    border-radius: 0 0.5rem 0.5rem 0.5rem;
    box-shadow: 0.25rem 0.25rem 0.6rem rgba(0,0,0,0.05), 0 0.5rem 1.125rem rgba(75,0,0,0.05);
    counter-increment: gradient-counter;
    margin-top: 1rem;
    min-height: 3rem;
    padding: 1rem 1rem 1rem 3rem;
    position: relative;
    font-size: 17px;
}
.step::before {
    counter-increment: count;
    content: counters(count, ".");
    background: #DAA520;
    border-radius: 1rem 1rem 0 1rem;
    height: 3rem;
    left: -1rem;
    overflow: hidden;
    position: absolute;
    top: -1rem;
    width: 3rem;
    align-items: flex-end;
    color: black;
    display: flex;
    font: 900 1.5em/1 'Montserrat';
    justify-content: flex-end;
    padding: 0.125em 0.25em;
    z-index: 1;
   
}

.aboutMe {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 60%;
    max-width: 750px;
    margin-top: 10px;
}

.aboutMeParagraph {
    font-size: 22px;
    display: flex;
    text-align: left;
    
}
.title {
    font-size: 50px;
}
